import React, { useEffect, useState } from "react";
import { ethers } from "ethers";
import { JsonRpcProvider } from "ethers/providers";

function App() {
  const [provider, setProvider] = useState(null);

  useEffect(() => {
    // Initialize ethers provider
    const initEthers = async () => {
      try {
        const ethProvider = new JsonRpcProvider("https://mainnet.infura.io/v3/your-infura-project-id");
        await ethProvider.getNetwork(); // Ensures provider is fully initialized
        setProvider(ethProvider);
      } catch (error) {
        console.error("Error initializing ethers provider:", error);
      }
    };

    initEthers();
  }, []);

  return (
    <div className="App">
      <h1>Ethers Example</h1>
      {provider ? (
        <p>Provider connected: {provider.connection.url}</p>
      ) : (
        <p>Loading...</p>
      )}
    </div>
  );
}

export default App;
